<template>
    <div class="center-selection">
        <el-input v-model="input" placeholder="请输入内容"></el-input>
        <div class="select" @click="buttonClick">
            <h3>查询</h3>
        </div>
        <div class="delete" v-show="deleteIsShow" @click="clearTable">
            <h3>清空</h3>
        </div>
        <div class="echarts4"></div>

    </div>
</template>

<script>
    import {mapState} from 'vuex'

    let myEchart
    export default {
        name: "index",
        data() {
            return {
                input: '',
                ch4: [],
                h2s: [],
                time: [],
                deleteIsShow: false
            }
        },
        methods: {
            clearTable() {
                myEchart.dispose()
                this.deleteIsShow = !this.deleteIsShow
            },
            createEcharts() {
                myEchart = this.$echarts.init(document.querySelector('.echarts4'), 'dark')
                const option = {
                    title: {
                        text: `${this.selectData.id}号设备气体检测情况`,
                        left: 'center'
                    },
                    grid: {
                        bottom: 40
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross',
                            animation: false,
                            label: {
                                backgroundColor: '#505765'
                            }
                        }
                    },
                    legend: {
                        data: ['硫化氢', '甲烷'],
                        left: 10
                    },
                    dataZoom: [
                        {
                            show: true,
                            realtime: true,
                            type: 'inside',
                            start: 0,
                            end: 100
                        }
                    ],
                    xAxis: [
                        {
                            type: 'category',
                            boundaryGap: false,
                            axisLine: {onZero: false},
                            data: this.time.map(d => {
                                return `${d.substring(0, 4)}年${d.substring(4, 6)}月${d.substring(6, 8)}日\n${d.substring(8, 10)}:${d.substring(10, 12)}:${d.substring(12)}`
                            })
                        }
                    ],
                    yAxis: [
                        {
                            name: '硫化氢浓度',
                            type: 'value'
                        },
                        {
                            name: '甲烷浓度',
                            nameLocation: 'start',
                            alignTicks: true,
                            type: 'value',
                            inverse: true
                        }
                    ],
                    series: [
                        {
                            name: '硫化氢',
                            type: 'line',
                            areaStyle: {},
                            lineStyle: {
                                width: 1
                            },
                            emphasis: {
                                focus: 'series'
                            },
                            markArea: {
                                silent: true,
                                itemStyle: {
                                    opacity: 0.3
                                },
                                // data: [
                                //     [
                                //         {
                                //             xAxis: '2009/9/12\n7:00'
                                //         },
                                //         {
                                //             xAxis: '2009/9/22\n7:00'
                                //         }
                                //     ]
                                // ]
                            },
                            data: this.h2s
                        },
                        {
                            name: '甲烷',
                            type: 'line',
                            yAxisIndex: 1,
                            areaStyle: {},
                            lineStyle: {
                                width: 1
                            },
                            emphasis: {
                                focus: 'series'
                            },
                            markArea: {
                                silent: true,
                                itemStyle: {
                                    opacity: 0.3
                                },
                                data: [
                                    [
                                        {
                                            xAxis: '2009/9/10\n7:00'
                                        },
                                        {
                                            xAxis: '2009/9/20\n7:00'
                                        }
                                    ]
                                ]
                            },
                            data: this.ch4
                        }
                    ]
                };
                myEchart.setOption(option)
            },
            buttonClick() {
                if (this.input.trim() === '') {
                    alert("输入不能为空")
                } else if (!isNaN(Number(this.input))) {

                    this.$store.dispatch('getSelectData',this.input.trim())

                } else {
                    alert('输入错误，只能输入数字')
                }
                this.input = ''
            },

        },
        computed: {
            ...mapState({
                selectData: (state) => {
                    return state.mockData.selectData
                }
            })
        },
        watch: {
            selectData(newValue) {
                if (newValue.id!==-1){
                    this.h2s = newValue.data.map(d => d.h2s)
                    this.ch4 = newValue.data.map(d => d.ch4)
                    this.time = newValue.data.map(d => d.time.toString())
                    if (this.$echarts.getInstanceByDom(document.querySelector('.echarts4'))===undefined) {
                        this.createEcharts()
                    } else {
                        myEchart.setOption({
                            title: {
                                text: `${this.selectData.id}号设备气体检测情况`,
                            },
                            xAxis: [
                                {
                                    data: this.time.map(d => {
                                        return `${d.substring(0, 4)}年${d.substring(4, 6)}月${d.substring(6, 8)}日\n${d.substring(8, 10)}:${d.substring(10, 12)}:${d.substring(12)}`
                                    })
                                }
                            ],
                            series: [
                                {
                                    data: this.h2s
                                },
                                {
                                    data: this.ch4
                                }
                            ]
                        })
                    }
                    this.deleteIsShow = true
                }else{
                    alert('输入的id不存在')
                }

            }
        }
    }
</script>

<style scoped>
    .center-selection {
        height: 367px;
        /*background-color: aqua;*/
        background-image: url("../../../../public/images/矢量智能对象 2.png"), url("../../../../public/images/line(1).png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        position: relative;
    }

    .el-input {

        position: absolute;
        top: 17%;
        left: 5%;

    }

    /deep/ .el-input__inner {
        background-color: rgba(13, 93, 155, 1);
        border: 1px solid rgba(74, 144, 226, 1);
        height: 40px;
        width: 242px;
    }

    /deep/ .el-input__inner:focus {
        background-color: white;
        box-shadow: 0 0 10px rgba(77, 191, 227, 1);
    }

    .select {
        background-image: url("../../../../public/images/矢量智能对象.png");
        background-repeat: no-repeat;
        height: 41px;
        width: 96px;
        position: absolute;
        top: 17%;
        left: 35%;

    }

    .delete {
        background-image: url("../../../../public/images/矢量智能对象.png");
        background-repeat: no-repeat;
        height: 41px;
        width: 96px;
        position: absolute;
        top: 17%;
        left: 50%;

    }

    .select:active {
        top: 17.2%;
        left: 35.2%;
    }

    .delete:active {
        top: 17.2%;
        left: 50.2%;
    }

    h3 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-55%);
        color: white;
        font-weight: bold;
        cursor: default;
    }

    .echarts4 {
        position: absolute;
        bottom: 5%;
        width: 90%;
        height: 60%;
        left: 50%;
        transform: translateX(-50%);

    }
</style>