<template>
    <div class="header">
        <div class="time">
            <h3 ref="showTime"></h3>
        </div>
    </div>
</template>

<script>
    export default {
        name: "top",
        methods:{
            startTime(){
                const today=new Date()
                const y=today.getFullYear()
                const m=today.getMonth()
                const d=today.getDate()
                const h=today.getHours()
                let mi=today.getMinutes()
                let  s=today.getSeconds()
                mi=this.checkTime(mi)
                s=this.checkTime(s)
                try {
                    this.$refs.showTime.innerHTML=`${y}年${m+1}月${d}日${h}时${mi}分${s}秒`
                }catch (e) {
                    
                }

            },
            checkTime(i){
                if(i<10){
                    i='0'+i
                }
                return i
            }
        },
        mounted() {
           setInterval(()=>{
               this.startTime()
           },500)
        }
    }
</script>

<style scoped>
    .header{
        position: relative;
        margin: 8px auto 74px auto;
        width: 1750px;
        height: 107px;
        background-image: url('../../public/images/top拷贝2.png');

    }
    .time{
        position: absolute;
        top: 95px;
        right: -44px;
        height: 70px;
        width: 408px;
        background-image: url('../../public/images/矢量智能对象 1.png');

    }
    h3 {
        color: white;
        position: absolute;
        width: 70%;
        top: 50%;
        left: 55%;
        transform: translate(-50%,-50%);
        font-size: 20px;

    }
</style>