<template>
    <div class="center-map">
        <div class="box">
            <div id="map"></div>
        </div>
    </div>
</template>

<script>
    import {mapState} from 'vuex'

    let map
    export default {
        name: "index",
        computed: {
            ...mapState({
                firstPoints: (state) => {
                    return state.mockData.jiangYangData.first
                },
                normalPoins: (state) => {
                    return state.mockData.jiangYangData.normal
                },
                secondPoints: (state) => {
                    return state.mockData.jiangYangData.second
                }
            }),

        },
        methods: {
            setCenter(value) {
                var point = new BMapGL.Point(value.geometry.coordinates[0], value.geometry.coordinates[1])
                map.setCenter(point)
                map.setZoom(21)

            },
            setCenter1(value) {
                this.setCenter(value)
            },
            createMap(Data) {
                map = new BMapGL.Map("map", {minZoom: 12});
                var point = new BMapGL.Point(105.444164806, 28.8754545325)
                map.centerAndZoom(point, 20);
                map.enableScrollWheelZoom(true);
                var zoomCtrl = new BMapGL.ZoomControl({
                    offset: new BMapGL.Size(10, 100)
                });  // 添加缩放控件
                map.addControl(zoomCtrl);
                map.setMapStyleV2({
                    styleId: '09a30fdfaad16eed8c678ca3cd9feda7'
                });
            },
            setPoints(){
                var view = new mapvgl.View({
                    map: map
                });
                var normalLayer = new mapvgl.PointLayer({
                    blend: 'lighter',
                    size: 15,
                    color: 'rgba(33,206,67,1)',
                    enablePicked: true,
                    autoSelect: true,
                    onClick: (e) => {
                        if (e.dataIndex !== -1) {
                            var infoWindow = new BMapGL.InfoWindow(`设备编号：${e.dataItem.properties.id}<br />地址：${e.dataItem.properties.area}<br />甲烷浓度：${e.dataItem.properties.ch4}<br />硫化氢浓度：${e.dataItem.properties.h2s}`, {title: 'Infomation:'});  // 创建信息窗口对象
                            map.openInfoWindow(infoWindow, new BMapGL.Point(e.dataItem.geometry.coordinates[0], e.dataItem.geometry.coordinates[1]));

                        }
                        return
                    }
                });
                var secondLayer = new mapvgl.PointLayer({
                    blend: 'lighter',
                    size: 15,
                    color: 'rgba(247,203,103,1)',
                    enablePicked: true,
                    autoSelect: true,
                    onClick: (e) => {
                        if (e.dataIndex !== -1) {
                            var infoWindow = new BMapGL.InfoWindow(`设备编号：${e.dataItem.properties.id}<br />地址：${e.dataItem.properties.area}<br />甲烷浓度：${e.dataItem.properties.ch4}<br />硫化氢浓度：${e.dataItem.properties.h2s}`, {title: 'Infomation:'});  // 创建信息窗口对象
                            map.openInfoWindow(infoWindow, new BMapGL.Point(e.dataItem.geometry.coordinates[0], e.dataItem.geometry.coordinates[1]));

                        }
                        return
                    }
                });
                var firstLayer = new mapvgl.PointLayer({
                    blend: 'lighter',
                    size: 18,
                    color: 'rgba(226,25,24,1)',
                    enablePicked: true,
                    autoSelect: true,
                    onClick: (e) => {
                        if (e.dataIndex !== -1) {
                            var infoWindow = new BMapGL.InfoWindow(`设备编号：${e.dataItem.properties.id}<br />地址：${e.dataItem.properties.area}<br />甲烷浓度：${e.dataItem.properties.ch4}<br />硫化氢浓度：${e.dataItem.properties.h2s}`, {title: 'Infomation:'});  // 创建信息窗口对象
                            map.openInfoWindow(infoWindow, new BMapGL.Point(e.dataItem.geometry.coordinates[0], e.dataItem.geometry.coordinates[1]));

                        }
                        return
                    }
                });
                var ripplefirstLayer = new mapvgl.RippleLayer({
                    size: 70,
                    unit: 'px',
                    color: 'rgba(255, 255, 255,1)',
                    duration: 2,
                });
                var ripplefirstLayer1 = new mapvgl.RippleLayer({
                    size: 70,
                    unit: 'px',
                    color: 'rgba(255,255,255,1)',
                    duration: 2,
                });
                view.addLayer(ripplefirstLayer);
                ripplefirstLayer.setData(this.firstPoints);
                view.addLayer(ripplefirstLayer1);
                ripplefirstLayer1.setData(this.firstPoints);
                view.addLayer(normalLayer)
                view.addLayer(firstLayer)
                view.addLayer(secondLayer)
                normalLayer.setData(this.normalPoins)
                secondLayer.setData(this.secondPoints)
                firstLayer.setData(this.firstPoints)
            }
        },
        watch: {
            firstPoints: {
                handler(newValue, oldValue) {
                    this.setPoints()
                }
            }
        },
        mounted() {
            this.$bus.$on('setCenter', this.setCenter)
            this.createMap()
        }
    }
</script>

<style scoped>
    .center-map {
        height: 486px;
        /*background-color: aqua;*/
        margin-bottom: 23px;
        background-image: url("../../../../public/images/矢量智能对象 2.png"), url("../../../../public/images/line(1).png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        /*overflow: hidden;*/
        position: relative;
    }

    #map {
        height: 110%;
        width: 100%;
    }

    .box {
        height: 100%;
        width: 100%;
        overflow: hidden;
    }

    button {
        position: absolute;
        left: -10px;
        top: 20px;
    }
</style>