import {requireHttp,requireSelect,requireMockSelect} from '../../api/index'


const state={
    jiangYangData:{},
    selectData:{}
}
const actions={
    async getJiangYangData(commit){
        const data=await requireHttp()
        commit.commit('GETJIANGYANGDATA',data)
    },
    async getSelectData(commit,id){
        const data=await requireSelect(id)
        commit.commit('GETSELECTDATA',data.data)
    }
    // async getSelectData(commit){
    //     const data=await requireSelect()
    //     commit.commit('GETSELECTDATA',data.data)
    // }
}
const mutations={
    GETJIANGYANGDATA(state,value){
        state.jiangYangData=value
    },
    GETSELECTDATA(state,value){
        state.selectData=value
    }
}

export default {
    state,
    actions,
    mutations,
}