<template>
    <div class="right-top">
        <div id="main"></div>
    </div>
</template>

<script>
    import {mapState} from "vuex";
        let myChart
    export default {
        name: "index",
        methods:{
           creatMyChart(){
               myChart=this.$echarts.init(document.getElementById('main'))
               const option = {
                    title:{
                        text:"当日一、二级报警点数据统计图\n",
                        textStyle: {
                            color: '#fff',
                        },
                        left:40,
                        top:15
                    },
                   legend: {
                       // top: 'bottom',
                       bottom:13,
                       textStyle: {
                           color:'#fff'
                       },

                   },
                   tooltip:{
                       trigger: 'item',
                       formatter: '{b}: {c} ({d}%)'
                   },
                   color:['rgb(218,0,1)','rgb(247,218,38)','rgb(1,146,63)'],
                   series: [
                       {
                           name: 'Nightingale Chart',
                           type: 'pie',
                           radius: ["20%", "60%"],
                           center: ['50%', '50%'],
                           itemStyle: {
                               borderRadius: 8
                           },
                            label:{
                               textBorderWidth:0
                            },
                           data:this.pie_infor.level
                       }
                   ]
               };
               myChart.setOption(option)
           },
        },

        computed:{
            ...mapState({
                pie_infor:(state)=>{
                    return state.mockData.jiangYangData.pie_infor
                }
            })
        },
        watch:{
            pie_infor(){
                if (this.$echarts.getInstanceByDom(document.querySelector('#main'))===undefined){
                    this.creatMyChart()
                }else{
                    myChart.setOption({
                        series:{ data:this.pie_infor.level}
                    })
                }

            }
        }
    }
</script>

<style scoped>
    .right-top{
        height: 304px;
        /*background-color: aqua;*/
        background-image: url("../../../../public/images/矢量智能对象 2.png"),url("../../../../public/images/line(1).png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }
    #main{
        height: 100%;
        width: 100%;
    }
</style>