<template>
    <div class="right-center">
        <div id="echart"></div>
    </div>
</template>

<script>
    import {mapState} from "vuex";
        let myChart
    export default {
        name: "index",
        methods:{
            createMyChart() {
                myChart=this.$echarts.init(document.getElementById('echart'))
                const option={
                    title:{
                        text:'当日甲烷气体数据统计图\n',
                        textStyle:{
                            color:'#fff'
                        },
                        left:50,
                        top:15
                    },
                    tooltip:{
                        trigger: 'item',
                        formatter: '{b}: {c} ({d}%)'
                    },
                    legend:{
                        orient:'vertical',
                        left: 60,
                        top:'35%',
                        textStyle: {
                            color: '#fff'
                        }
                    },
                    color:['rgb(218,0,1)','rgb(247,218,38)','rgb(1,146,63)'],
                    series: [
                        {
                            name: 'Access From',
                            type: 'pie',
                            radius: ['20%','70%'],
                            center:['60%','55%'],
                            labelLine:{
                                show:false
                            },
                            itemStyle: {
                                borderRadius: 8
                            },
                            label:{
                                show:false
                            },
                            data:this.pie_infor.ch4
                        }
                    ]
                }
                myChart.setOption(option)
            }
        },
        computed:{
            ...mapState({
                pie_infor:(state)=>{
                    return state.mockData.jiangYangData.pie_infor
                }
            })
        },
        watch:{
            pie_infor(){
                if (this.$echarts.getInstanceByDom(document.querySelector('#echart'))===undefined){
                    this.createMyChart()
                }else{
                    myChart.setOption({
                        series:{  data:this.pie_infor.ch4}
                    })
                }
            }
        }
    }
</script>

<style scoped>
    .right-center{
        margin-top: 44px;
        height: 240px;
        width: 100%;
        /*background-color: aqua;*/
        background-image: url("../../../../public/images/矢量智能对象 2.png"),url("../../../../public/images/line(1).png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }
    #echart{
        height: 100%;
    }
</style>